import React, { useEffect, useState } from 'react';
import './Hero.css';
import '../OurStack.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAddressCard, faCircleCheck, faStar } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
import ReactPlayer from 'react-player';
import { APP_NAME, PROMO_VIDEO } from '../../../../shared/utility';
import { isMobile } from 'react-device-detect';

function Hero() {
	const [goUp, setGoUp] = useState(false);

	useEffect(() => {
		const onPageScroll = () => {
			if (window.scrollY > 600) {
				setGoUp(true);
			} else {
				setGoUp(false);
			}
		};
		window.addEventListener('scroll', onPageScroll);

		return () => {
			window.removeEventListener('scroll', onPageScroll);
		};
	}, []);

	return (

		<div className="flowhero-section-i" id="flowheroherohero" >
			<div style={{ display: 'flex', flexDirection: 'row', width: '100%', alignItems: 'center' }}>
				<div className="about-image-content-i" style={{ width: '50%' }}>
					<div className="about-video-content" style={{ flexDirection: 'column' }}>
						<ReactPlayer url={PROMO_VIDEO} />
					</div>
				</div>

				<div className="text-section" >
					<h2 className="text-title" >{`Workflow automation made simpler`}</h2>
					<p className="text-descritpion">{`We automate your complex business processes and functions using our easy to understand interface. Built on the cloud, concentrate on what your business do best and leave us the worry of data gathering and information delivery to your customers.`}</p>
				</div>

			</div>

			<Hero2 />
		</div>
	);
}

export default Hero;

